export const clean = (obj) =>
  Object.keys(obj)
    .filter((k) => obj[k] != null)
    .reduce((a, k) => ({ ...a, [k]: obj[k] }), {})

export const styleDiv = (getDiv, style) => {
  const div = getDiv()
  Object.keys(style).forEach((k) => (div.style[k] = style[k]))
}

export const isDef = (x) => x !== null && x !== undefined
